import React from 'react';
import { NavLink} from 'react-router-dom';
import { Menu, Dropdown } from 'semantic-ui-react'
 
const Header = () => {
    
    return(
        <Menu pointing secondary>
          <NavLink to ='/' exact className="item" activeClassName="active item">
            Home
          </NavLink>
          <NavLink to ='/about' className="item">
            About
          </NavLink>
          <NavLink to ='/rn' className="item">
            Code
          </NavLink>
          <Dropdown item text='Course Notes'>
            <Dropdown.Menu>
              <Dropdown.Item><a href="https://matg1058.netlify.app/0_intro.html">Numerical Optimization</a></Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </Menu>
    )
}
export default Header;